// Load Libraries
var $ = require('jquery');
var mailcheck = require('mailcheck');

// MailCheck Config
var MAILCHECK_INPUT         = 'input[type=email]';      // Selector for fields to validate
var MAILCHECK_CONTAINER     = '.form__field';           // Selector for container that holds the field+suggestion
var MAILCHECK_SUGGESTION    = '.mailcheck__email';      // Selector to populate with the suggestion
var MAILCHECK_ACCEPT        = '.mailcheck__accept';     // Selector that can be selected to accept suggestion
var MAILCHECK_SUGGESTED     = 'form__field--mailcheck'; // Class to add to container when a suggestion is available

// Domain Suggestions
