    /* Custom brand domains */
    'iamota.com', 'pne.ca', 'sugoi.com',

    /* Default domains included */
    'facebook.com', 'gmail.com', 'gmx.com', 'googlemail.com', 'google.com', 'hotmail.com', 'mac.com',
    'me.com', 'mail.com', 'msn.com', 'live.com', 'yahoo.com',

