    'queensu.ca', 'ualberta.ca', 'ucalgary.ca', 'unb.ca', 'usask.ca', 'uwaterloo.ca', 'uwo.ca', 'uqam.ca',

    /* Canadian domains */
    'yahoo.ca', 'live.ca', 'msn.ca', 'yahoo.ca', 'hotmail.ca',

    /* United States ISP domains */
    'aol.com', 'att.net', 'comcast.net', 'bellsouth.net', 'charter.net', 'comcast.net', 'cox.net', 'earthlink.net',
    'juno.com', 'sbcglobal.net', 'verizon.net', 'optonline.net', 'roadrunner.com', 'juno.com',

    /* United States Universities */
    'ucla.edu',

    /* British ISP domains */
    'btinternet.com', 'virginmedia.com', 'blueyonder.co.uk', 'freeserve.co.uk', 'live.co.uk',
    'ntlworld.com', 'o2.co.uk', 'orange.net', 'sky.com', 'talktalk.co.uk', 'tiscali.co.uk',
